import { Component, OnInit } from '@angular/core';
import { TitleCasePipe } from '@angular/common';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ScheduleShopService } from 'src/app/core/services/schedule-shop.service';
import { ToastNotificationService } from 'src/app/core/services/toast-notification.service';
import { ScheduleScheduleService } from 'src/app/core/services/schedule-schedule.service';

@Component({
  selector: 'app-shop-details-page',
  templateUrl: './shop-details-page.component.html',
  styleUrls: ['./shop-details-page.component.scss']
})
export class ShopDetailsPageComponent implements OnInit {
  searchTerm: string;

  shops = [
    {
      "id": 1,
      "email": "demo21@gmail.com",
      "mobile": "8783459890",
      "firstName": "john",
      "lastName": "hi",
      "parentId": "478d3543-d30d-43f2-9559-a080f7b93025",
      "isRegistered": true,
      "legalName": "NH_SHOP",
      "accountMethod": "Accounts With Stock Management",
      "businessType": "Proprietorship",
      "gstinNo": "22",
      "gstType": "Regular",
      "compositeType": "Trader",
      "homeno": "2",
      "pincode": "390011",
      "address": "10,abcd road",
      "city": "vadodara",
      "district": "vadodara",
      "landmark": "vadodara",
      "state": "GUJARAT",
      "country": "INDIA"
    }
  ]

  public shopForm: FormGroup;
  public currentUser = JSON.parse(localStorage.getItem("currentUser"))
  public ShopListResponse: any;

  constructor(private formBuilder: FormBuilder, private ScheduleShopService: ScheduleShopService, private toastService: ToastNotificationService,
    private titleCasePipe: TitleCasePipe, private scheduleScheduleService: ScheduleScheduleService,) { }

  ngOnInit(): void {
    this.getShopList();
  }

  getShopList() {
    this.ScheduleShopService.getShopList({}, this.currentUser.id).then((res) => {
      if (res.data.rows) {
        this.ShopListResponse = res.data.rows;
      }
    }, (err) => {
      if (err.error.expose) {
        this.ShopListResponse = [];
        this.toastService.openErrorSnackBar(this.titleCasePipe.transform(err.error.error_message));
      }
      else {
        this.toastService.openErrorSnackBar("Something Went Wrong.");
      }
    })
  }

  isActiveShop(shopId, isActiveById) {
    const isActive = !isActiveById;
    const body = { "isActive": isActive };

    this.ScheduleShopService.isActiveShop(body, shopId).then((res: any) => {
      if (res.success) {
        if (isActive) {
          this.toastService.openSnackBar("Shop status changed to active")
          this.getShopList()
        } else {
          this.toastService.openSnackBar("Shop status changed to inactive")
          this.getShopList()
        }
      }
    })
  }

  searchShop() {
    if (this.searchTerm.trim() === '') {
      this.getShopList();
    } else {
      this.ScheduleShopService.getShopList({}, this.currentUser.id).then((res) => {
        this.ShopListResponse = res.data.rows.filter(item => {
            return (
                // item.scheduleName.toLowerCase().includes(this.searchTerm.toLowerCase()) ||
                item.email && item.email.toLowerCase().includes(this.searchTerm.toLowerCase()) ||
                (item.client.tradeName && item.client.tradeName.toLowerCase().includes(this.searchTerm.toLowerCase()))
                // item.appointmentType.locationType.toLowerCase().includes(this.searchTerm.toLowerCase())
            );
        });
    });
    
      
      if (this.ShopListResponse.length === 0) {
        this.toastService.openErrorSnackBar(this.searchTerm + " is not found");
      }
    }
  }
}


